.ecommerce-gallery .lightbox{position:relative;overflow:hidden}.ecommerce-gallery .lightbox img{height:450px;-o-object-fit:contain;object-fit:contain}@media(max-width: 768px){.ecommerce-gallery .lightbox img{height:150px}}.ecommerce-gallery .lightbox img.animating{position:absolute;left:0;top:0;z-index:-1;width:100%}.ecommerce-gallery img:not(.ecommerce-disabled):not(.ecommerce-gallery-main-img){cursor:-webkit-zoom-in;cursor:zoom-in;border-bottom:2px solid transparent}.ecommerce-gallery img:not(.ecommerce-disabled):not(.ecommerce-gallery-main-img).active{border-color:orange}.ecommerce-gallery .ecommerce-gallery-main-img{display:none;-webkit-transition:all .3s;transition:all .3s;-webkit-transform-origin:center center;transform-origin:center center}.ecommerce-gallery .ecommerce-gallery-main-img.active{display:block;-webkit-transition:none;transition:none}@media(max-width: 768px){.ecommerce-gallery .multi-carousel:not(.vertical) .multi-carousel-inner .multi-carousel-item img{max-height:50px}}.ecommerce-gallery.vertical .lightbox{height:100%;display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;-webkit-box-align:center;-ms-flex-align:center;align-items:center}.ecommerce-gallery.vertical .lightbox img{height:350px}@media(max-width: 768px){.ecommerce-gallery.vertical .lightbox img{height:100px}}@media(max-width: 768px){.ecommerce-gallery.vertical .multi-carousel.vertical{height:100px}}